import Vue from 'vue'
import Router from 'vue-router'
const Relationship = () => import('../components/Relationship')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/relationship'
    },
    {
      path: '/relationship',
      name: 'relationship',
      component: Relationship
    }
  ]
})



